    --ff-regular: "Roboto", 'Times New Roman', serif;
    --ff-alt: "PT Serif", "Roboto", 'Times New Roman', serif;
    --fc-regular: rgb(236, 236, 236);
    --fc-error: #ff0000;
    --fs-small: clamp(0.75rem, 0.5rem + 1.5vw, 1rem);
    --fs-regular: clamp(1rem, 0.5rem + 2vw, 1.2rem);
    --fs-large: clamp(1.5rem, 0.6rem + 2.5vw, 1.75rem);    
    --fs-xlarge: clamp(1.5rem, 0.6rem + 3vw, 2.5rem);
    --fs-xxlarge: clamp(2.5rem, 0.8rem + 5vw, 3rem);
    --fw-thin: 300;
    --fw-regular: 500;
    --fw-bold: 700;
    --fw-xbold: 800;
    /* --bg-color: rgba(16, 94, 184, 1.0);
    --bg-color-alt: rgb(34, 116, 209); */
    --bg-color: rgb(13, 83, 163);
    --bg-color-alt: rgb(30, 104, 189);
    --bg-color-highlight: rgb(21, 105, 201);
    --bg-actColor:rgba(117, 116, 116, 0.4);
    --bg-button: rgba(189, 3, 3, 0.61);
    --bg-buttonAlt: rgba(255, 44, 44, 0.61);
    --brdr-color: rgb(221, 221, 221);
    --pseudo-display: none;
}

*{
    font-family: var(--ff-regular);    
    font-size: var(--fc-regular);
    font-weight: var(--fw-regular);
    color: var(--fc-regular);
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    list-style: none;
    user-select: none;
}


body{
    height: 100vh;
    background-color: var(--bg-color);
    display: grid;
    grid-template-columns: minmax(min-content, 225px) 1fr;
    grid-template-rows: 1fr calc(var(--fs-xlarge)*1.8);
    grid-template-areas:
        "nav main"
        "nav aside";
}

img{
    height: calc(var(--fs-small)*1.6);
    -webkit-user-drag: none;
}

::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-track{
    background-color: #d6dee14b;
    border-radius: 20px;
}


::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 3px solid transparent;
    background-clip: content-box;
  }

::-webkit-scrollbar-thumb:hover{
    background-color: #ffffff;    
}

nav{
    grid-area: nav;
    display: grid;
    background-color: var(--bg-color-alt);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    gap: var(--fs-xlarge);
    grid-template-rows: min-content 1fr min-content;
    padding: var(--fs-small) var(--fs-regular);
    padding-bottom: calc(var(--fs-small)/1.5);
}

.navMain{
    display: grid;
    width: max-content;
    gap: calc(var(--fs-small)/2);
}

.navProjects{
    display: grid;
    gap: var(--fs-large);
    background-color: var(--bg-color);
    padding-block: calc(var(--fs-small)/2);
    padding-inline: var(--fs-small);
    border-radius: 10px;
    overflow-y: auto;
}

.projectTask{
    font-weight: var(--fw-thin);
    border-radius: 5px;
    padding-inline: 10px;
    padding-block: 1px;
    /* width: max-content; */
}

ul,
li{
    transition: all 0.04s ease-in-out;
}

li.navItem:hover{
    scale: 1.075;
    font-weight: var(--fw-xbold);
    cursor: pointer;
}

li.navItem:active{
    scale: 1;
    font-weight: var(--fw-xbold);
    cursor: pointer;
}

.navItem.home{
    margin-block-start: calc(var(--fs-small)/1.5);
}

.projectListInfo{
    display: grid;
    gap: calc(var(--fs-small)/5);
}

.projectBlock{
    height: fit-content;
    display: grid;
    gap: calc(var(--fs-small)/4);
}

.projectTitle{
    width: max-content;
    font-weight: var(--fw-bold);
    /* background-image: linear-gradient(white, white);
    background-size: 0% 0.1em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
    transition: background-size 0.2s ease-in-out; */
    }

/* .projectTitle:hover,
.projectTitle:focus,
.projectTitle:active {
    background-size: 100% 0.1em;
} */
 .projectTitle:hover{
    scale: 1.02
 }

.projectTitle:active{
    scale: 0.975;
}

.icon,
.newTask{
    transition: all 0.02s ease-in-out;
}

.icon:hover{
    scale: 1.1;
    cursor: pointer;
}
.icon:active{
    scale: 1;
}

.newTask:hover{
    scale: 1.05;
    cursor: pointer;
}
.newTask:active{
    border-width: 3px;
    scale: 1;
}

aside{
    grid-area: aside;
    position: relative;
    margin-block-end: calc(var(--fs-small)/1);
}

.newTask{
    position: absolute;
    transform: translate(-50%, 0%);
    bottom: 0%;
    left: 50%;
    height: var(--fs-xxlarge);
    padding: calc(var(--fs-small)/2.25);
    background-color: var(--bg-color-alt);
    border: 2px solid white;
    border-radius: 50%;
}

aside,
.actionMain{
    font-size: var(--fs-small);
}

.actionMain{
    display: flex;
    justify-content: space-between;
    padding-inline: 10px;
}

main{
    grid-area: main;
    background-color: var(--bg-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

fieldset{
    display: grid;
    gap: calc(var(--fs-small)/1.5);
}

input,
textarea{
    position: relative;
    padding: calc(var(--fs-small)/4) calc(var(--fs-small)/4);
    border-radius: 5px;
    color: black;
}

::placeholder{
    color: darkgrey;
}


legend{
    margin-block-end: var(--fs-small);
    font-size: var(--fs-large);
    font-weight: var(--fw-bold);
    text-align: center;
}

.formControl{
    position: relative;
    display: grid;
    gap: calc(var(--fs-small)/8);
}

button{
    transition: all 0.02s ease-in-out;
}

button:hover{
    scale: 1.05;
    cursor: pointer;
}

button:active{
    scale: 0.985;
}

.submitBtn{
    margin-block-start: var(--fs-small);
    padding-block: calc(var(--fs-small)/2);
    border: 2px solid grey;
    border-radius: 15px;
    color: black;
}

.submitBtn:disabled{
    background-color: buttonface;
    border: 2px solid rgb(172, 42, 42);
}

.cancelBtn{
    position: absolute;
    bottom: calc(var(--fs-regular)*-1 - 5px);
    left:0;
    font-size: var(--fs-small);
    color: var(--fc-regular);
    text-decoration: underline;
    background: none;
}

.cancelBtn:hover{
    scale: 1.05;
    font-weight: var(--fw-bold);
    cursor: pointer;
}
.cancelBtn:active{
    border-width: 3px;
    scale: 1;
}

.error{
    display: none;
    content: '';
    background-color: red;
    position: absolute;
    color: var(--fc-error);
    height: calc(100% - var(--fs-regular) - var(--fs-small)/8);
    width: 3px;
    top: calc(var(--fs-regular) + var(--fs-small)/8);
    left: -8px;
}

.errorDescription{
    display: none;
    height: var(--fs-regular);
    position: absolute;
    left: 0;
    top: -5px;
    bottom: calc(var(--fs-small)/4);
    color: var(--fc-error);
}

.dashboard{
    display: grid;
    grid-template-rows: 1fr 1fr;
    height:100%;
    width: 100%;
    padding:var(--fs-regular);
    gap: var(--fs-regular);
}

.dashUpcoming,
.dashClock,
.dashProjectRow{
    border-radius: 25px;
    padding: var(--fs-regular);
}

.dashTopRow{
    display: grid;
    grid-template-columns: 1fr min-content;
    align-items: center;
    justify-content: space-evenly;
    gap: var(--fs-regular);

}

.dashUpcoming,
.dashProjectList{
    height: 100%;
    background-color: var(--bg-color-alt);
    flex: 2;
    display: grid;
    gap: 5px;
    grid-template-rows: min-content 1fr;
}

.upcomingTitle,
.projectListTitle{
    font-weight: var(--fw-bold);
}

.upcomingTaskArea,
.projectListArea{
    height: 100%;
    background-color: var(--bg-color);
    border-radius: 20px;
    padding-inline: var(--fs-regular);
    padding-block: var(--fs-small);
}

.upcomingTaskBlock,
.projectListBlock{
    display: flex;
    gap: var(--fs-regular);
    padding-inline: 10px;
    padding-block: 3px;
    border-radius: 7.5px;
    align-items: center;
    justify-content: space-between;
}

.upcomingTaskBlock:hover,
.projectListBlock:hover,
.projectTask:hover{
    scale: 1.01;
    background-color: var(--bg-color-highlight);
    cursor: pointer;
}

.upcomingTaskBlock:active,
.projectListBlock:active,
.projectTask:active{
    scale: 1.00;
    background-color: var(--bg-color-highlight);
}


.upcomingTaskName,
.nextProjectListTask,
.upcomingTaskDue,
.projectListName{
    font-weight: var(--fw-thin);
}

.upcomingTaskName:hover,
.nextProjectListTask:hover,
.upcomingTaskDue:hover,
.projectListName:hover{
    cursor: pointer;
}

.dashClock{
    background-color: var(--bg-color-alt);
    height: 100%;
    aspect-ratio: 1;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 1fr min-content;
    grid-template-rows: 1fr min-content;
    grid-template-areas:
        "day year"
        "month month"
    ;

}

.clockMonth{
    grid-area: month;
    font-size: 6vh;
}

.clockDay{
    grid-area: day;
    font-size: 19vh;
}

.clockYear{
    grid-area: year;
    writing-mode:vertical-rl;
    transform: rotate(180deg);
    font-size: 6vh;
}

.dashProjectRow{
    background-color: var(--bg-color-alt);
}


