 'Times New Roman', serif;\n    --fc-regular: rgb(236, 236, 236);\n    --fc-error: #ff0000;\n    --fs-small: clamp(0.75rem, 0.5rem + 1.5vw, 1rem);\n    --fs-regular: clamp(1rem, 0.5rem + 2vw, 1.2rem);\n    --fs-large: clamp(1.5rem, 0.6rem + 2.5vw, 1.75rem);    \n    --fs-xlarge: clamp(1.5rem, 0.6rem + 3vw, 2.5rem);\n    --fs-xxlarge: clamp(2.5rem, 0.8rem + 5vw, 3rem);\n    --fw-thin: 300;\n    --fw-regular: 500;\n    --fw-bold: 700;\n    --fw-xbold: 800;\n    /* --bg-color: rgba(16, 94, 184, 1.0);\n    --bg-color-alt: rgb(34, 116, 209); */\n    --bg-color: rgb(13, 83, 163);\n    --bg-color-alt: rgb(30, 104, 189);\n    --bg-color-highlight: rgb(21, 105, 201);\n    --bg-actColor:rgba(117, 116, 116, 0.4);\n    --bg-button: rgba(189, 3, 3, 0.61);\n    --bg-buttonAlt: rgba(255, 44, 44, 0.61);\n    --brdr-color: rgb(221, 221, 221);\n    --pseudo-display: none;\n}\n\n*{\n    font-family: var(--ff-regular);    \n    font-size: var(--fc-regular);\n    font-weight: var(--fw-regular);\n    color: var(--fc-regular);\n    margin: 0;\n    padding: 0;\n    border: 0;\n    box-sizing: border-box;\n    list-style: none;\n    user-select: none;\n}\n\n\nbody{\n    height: 100vh;\n    background-color: var(--bg-color);\n    display: grid;\n    grid-template-columns: minmax(min-content, 225px) 1fr;\n    grid-template-rows: 1fr calc(var(--fs-xlarge)*1.8);\n    grid-template-areas:\n        \"nav main\"\n        \"nav aside\";\n}\n\nimg{\n    height: calc(var(--fs-small)*1.6);\n    -webkit-user-drag: none;\n}\n\n::-webkit-scrollbar{\n    width: 10px;\n}\n\n::-webkit-scrollbar-track{\n    background-color: #d6dee14b;\n    border-radius: 20px;\n}\n\n\n::-webkit-scrollbar-thumb {\n    background-color: #d6dee1;\n    border-radius: 20px;\n    border: 3px solid transparent;\n    background-clip: content-box;\n  }\n\n::-webkit-scrollbar-thumb:hover{\n    background-color: #ffffff;    \n}\n\nnav{\n    grid-area: nav;\n    display: grid;\n    background-color: var(--bg-color-alt);\n    border-top-right-radius: 25px;\n    border-bottom-right-radius: 25px;\n    gap: var(--fs-xlarge);\n    grid-template-rows: min-content 1fr min-content;\n    padding: var(--fs-small) var(--fs-regular);\n    padding-bottom: calc(var(--fs-small)/1.5);\n}\n\n.navMain{\n    display: grid;\n    width: max-content;\n    gap: calc(var(--fs-small)/2);\n}\n\n.navProjects{\n    display: grid;\n    gap: var(--fs-large);\n    background-color: var(--bg-color);\n    padding-block: calc(var(--fs-small)/2);\n    padding-inline: var(--fs-small);\n    border-radius: 10px;\n    overflow-y: auto;\n}\n\n.projectTask{\n    font-weight: var(--fw-thin);\n    border-radius: 5px;\n    padding-inline: 10px;\n    padding-block: 1px;\n    /* width: max-content; */\n}\n\nul,\nli{\n    transition: all 0.04s ease-in-out;\n}\n\nli.navItem:hover{\n    scale: 1.075;\n    font-weight: var(--fw-xbold);\n    cursor: pointer;\n}\n\nli.navItem:active{\n    scale: 1;\n    font-weight: var(--fw-xbold);\n    cursor: pointer;\n}\n\n.navItem.home{\n    margin-block-start: calc(var(--fs-small)/1.5);\n}\n\n.projectListInfo{\n    display: grid;\n    gap: calc(var(--fs-small)/5);\n}\n\n.projectBlock{\n    height: fit-content;\n    display: grid;\n    gap: calc(var(--fs-small)/4);\n}\n\n.projectTitle{\n    width: max-content;\n    font-weight: var(--fw-bold);\n    /* background-image: linear-gradient(white, white);\n    background-size: 0% 0.1em;\n    background-position-y: 100%;\n    background-position-x: 0%;\n    background-repeat: no-repeat;\n    transition: background-size 0.2s ease-in-out; */\n    }\n\n/* .projectTitle:hover,\n.projectTitle:focus,\n.projectTitle:active {\n    background-size: 100% 0.1em;\n} */\n .projectTitle:hover{\n    scale: 1.02\n }\n\n.projectTitle:active{\n    scale: 0.975;\n}\n\n.icon,\n.newTask{\n    transition: all 0.02s ease-in-out;\n}\n\n.icon:hover{\n    scale: 1.1;\n    cursor: pointer;\n}\n.icon:active{\n    scale: 1;\n}\n\n.newTask:hover{\n    scale: 1.05;\n    cursor: pointer;\n}\n.newTask:active{\n    border-width: 3px;\n    scale: 1;\n}\n\naside{\n    grid-area: aside;\n    position: relative;\n    margin-block-end: calc(var(--fs-small)/1);\n}\n\n.newTask{\n    position: absolute;\n    transform: translate(-50%, 0%);\n    bottom: 0%;\n    left: 50%;\n    height: var(--fs-xxlarge);\n    padding: calc(var(--fs-small)/2.25);\n    background-color: var(--bg-color-alt);\n    border: 2px solid white;\n    border-radius: 50%;\n}\n\naside,\n.actionMain{\n    font-size: var(--fs-small);\n}\n\n.actionMain{\n    display: flex;\n    justify-content: space-between;\n    padding-inline: 10px;\n}\n\nmain{\n    grid-area: main;\n    background-color: var(--bg-color);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\nfieldset{\n    display: grid;\n    gap: calc(var(--fs-small)/1.5);\n}\n\ninput,\ntextarea{\n    position: relative;\n    padding: calc(var(--fs-small)/4) calc(var(--fs-small)/4);\n    border-radius: 5px;\n    color: black;\n}\n\n::placeholder{\n    color: darkgrey;\n}\n\n\nlegend{\n    margin-block-end: var(--fs-small);\n    font-size: var(--fs-large);\n    font-weight: var(--fw-bold);\n    text-align: center;\n}\n\n.formControl{\n    position: relative;\n    display: grid;\n    gap: calc(var(--fs-small)/8);\n}\n\nbutton{\n    transition: all 0.02s ease-in-out;\n}\n\nbutton:hover{\n    scale: 1.05;\n    cursor: pointer;\n}\n\nbutton:active{\n    scale: 0.985;\n}\n\n.submitBtn{\n    margin-block-start: var(--fs-small);\n    padding-block: calc(var(--fs-small)/2);\n    border: 2px solid grey;\n    border-radius: 15px;\n    color: black;\n}\n\n.submitBtn:disabled{\n    background-color: buttonface;\n    border: 2px solid rgb(172, 42, 42);\n}\n\n.cancelBtn{\n    position: absolute;\n    bottom: calc(var(--fs-regular)*-1 - 5px);\n    left:0;\n    font-size: var(--fs-small);\n    color: var(--fc-regular);\n    text-decoration: underline;\n    background: none;\n}\n\n.cancelBtn:hover{\n    scale: 1.05;\n    font-weight: var(--fw-bold);\n    cursor: pointer;\n}\n.cancelBtn:active{\n    border-width: 3px;\n    scale: 1;\n}\n\n.error{\n    display: none;\n    content: '';\n    background-color: red;\n    position: absolute;\n    color: var(--fc-error);\n    height: calc(100% - var(--fs-regular) - var(--fs-small)/8);\n    width: 3px;\n    top: calc(var(--fs-regular) + var(--fs-small)/8);\n    left: -8px;\n}\n\n.errorDescription{\n    display: none;\n    height: var(--fs-regular);\n    position: absolute;\n    left: 0;\n    top: -5px;\n    bottom: calc(var(--fs-small)/4);\n    color: var(--fc-error);\n}\n\n.dashboard{\n    display: grid;\n    grid-template-rows: 1fr 1fr;\n    height:100%;\n    width: 100%;\n    padding:var(--fs-regular);\n    gap: var(--fs-regular);\n}\n\n.dashUpcoming,\n.dashClock,\n.dashProjectRow{\n    border-radius: 25px;\n    padding: var(--fs-regular);\n}\n\n.dashTopRow{\n    display: grid;\n    grid-template-columns: 1fr min-content;\n    align-items: center;\n    justify-content: space-evenly;\n    gap: var(--fs-regular);\n\n}\n\n.dashUpcoming,\n.dashProjectList{\n    height: 100%;\n    background-color: var(--bg-color-alt);\n    flex: 2;\n    display: grid;\n    gap: 5px;\n    grid-template-rows: min-content 1fr;\n}\n\n.upcomingTitle,\n.projectListTitle{\n    font-weight: var(--fw-bold);\n}\n\n.upcomingTaskArea,\n.projectListArea{\n    height: 100%;\n    background-color: var(--bg-color);\n    border-radius: 20px;\n    padding-inline: var(--fs-regular);\n    padding-block: var(--fs-small);\n}\n\n.upcomingTaskBlock,\n.projectListBlock{\n    display: flex;\n    gap: var(--fs-regular);\n    padding-inline: 10px;\n    padding-block: 3px;\n    border-radius: 7.5px;\n    align-items: center;\n    justify-content: space-between;\n}\n\n.upcomingTaskBlock:hover,\n.projectListBlock:hover,\n.projectTask:hover{\n    scale: 1.01;\n    background-color: var(--bg-color-highlight);\n    cursor: pointer;\n}\n\n.upcomingTaskBlock:active,\n.projectListBlock:active,\n.projectTask:active{\n    scale: 1.00;\n    background-color: var(--bg-color-highlight);\n}\n\n\n.upcomingTaskName,\n.nextProjectListTask,\n.upcomingTaskDue,\n.projectListName{\n    font-weight: var(--fw-thin);\n}\n\n.upcomingTaskName:hover,\n.nextProjectListTask:hover,\n.upcomingTaskDue:hover,\n.projectListName:hover{\n    cursor: pointer;\n}\n\n.dashClock{\n    background-color: var(--bg-color-alt);\n    height: 100%;\n    aspect-ratio: 1;\n    display: grid;\n    align-items: center;\n    justify-items: center;\n    grid-template-columns: 1fr min-content;\n    grid-template-rows: 1fr min-content;\n    grid-template-areas:\n        \"day year\"\n        \"month month\"\n    ;\n\n}\n\n.clockMonth{\n    grid-area: month;\n    font-size: 6vh;\n}\n\n.clockDay{\n    grid-area: day;\n    font-size: 19vh;\n}\n\n.clockYear{\n    grid-area: year;\n    writing-mode:vertical-rl;\n    transform: rotate(180deg);\n    font-size: 6vh;\n}\n\n.dashProjectRow{\n    background-color: var(--bg-color-alt);\n}\n\n\n"],"sourceRoot":""}]);
